<template>
  <div class="map-contant">
    <slot></slot>
  </div>
</template>
<script>
// -------------- 初始化 -------------------
import InitMapMixin from "../../components/AMap/mixins/initmap"
// -------------- AMap ----------------------
import {addDEForHeat} from "../../components/AMap/DistricExplorer/index";
import {addBroadcast} from "../../components/AMap/MassMark/broadcast";
import {addMassMark, updateMassmark} from "../../components/AMap/MassMark/forindex";
import {addMengBan} from "../../components/AMap/DistrictLayer";
// --------------- ws -----------------------
import wsMixin from "../../components/websocket/wsMixin";

export default {
  mixins: [InitMapMixin, wsMixin],
  mounted() {
    addDEForHeat(this.mapInfo.adcode, this.map, this, 11);
    this.socketInit(this.$wsUrlWork, this.onMessage);
    addMengBan(this.map, this.adcode)
    this.map.setRotation(65)
  },
  data() {
    return {
      dataContent: [],
      p: undefined
    }
  },
  methods: {
    //   ------------- ws ---------------
    /**
     * 接收消息后
     * @param e
     */
    onMessage: function (e) {
      let message = JSON.parse(e.data);
      let ml = message.data.mobile_list;
      for (let i = 0; i < ml.length; i++) {
        let lnglat = ml[i].lnglat;
        ml[i].lnglat = [Number(lnglat.split(",")[0]), Number(lnglat.split(",")[1])]
        this.dataContent.push(ml[i]);
      }
      if (this.p === undefined) {
        this.p = addMassMark(this.map, this.dataContent, this);
      } else {
        updateMassmark(this.map, this.dataContent, this.p);
      }
      this.$emit("getWorkPhoneNum", message.data.count);
    }
  },
  props: {
    massmark: {
      type: Array,
      default: () => {
        return []
      }
    }
  },
  watch:{
    massmark:function (val) {
      if (this.q === undefined) {
        this.q = addBroadcast(this.map, val, this);
      } else {
        updateMassmark(this.map, val, this.q);
      }
    }
  }
};
</script>
<style scoped>
.map-contant {
  height: 100%;
  width: 100%;
}
</style>
