export function addMengBan(map,now_adcode){
    var colorsMengban = {};
    var getColorBySoc = function (adcode, this_adcode) {
        if (adcode) {
            if (!colorsMengban[adcode]) {
                if (adcode === this_adcode) {
                    return null;
                }
                return '#033447'
                // return '#FFFFFF'
            }
        } else {
            return null
        }
    };
    var disCountry = new AMap.DistrictLayer.Country({
        zIndex: 999,
        SOC: 'CHN',
        depth: 2,
        styles: {
            'fill': function (props) {
                return getColorBySoc(props.adcode, now_adcode)
            }
        }
    });
    map.add(disCountry);
}