<template>
  <div className="chartSize"></div>
</template>

<script>
import resize from "../../../components/echart/mixins/resize";

export default {
  mixins: [resize],
  props: {
    currentData: {
      type: Array,
      default: function () {
        return  [1, 2, 3];
      }
    },
    title: {
      type: String,
      default: "图标标题"
    },
    initData: {
      type: Array,
      default: () => {
        return [];
      }
    },
    loading: {
      type: Boolean,
      default: false
    },
    chartName: {
      type: String,
      default: "年统计"
    }
  },
  data() {
    return {
      chartData: [1, 2, 3],
    };
  },
  computed: {
    option: function () {
      return {
        title: {
          text: '年统计',
          textStyle: {
            color: '#FFFFFF'
          },
          left: 'center',
          top:0
        },
        xAxis: {
          type: 'category',
          boundaryGap: false,
          data: ['一月', '二月', '三月',
            '四月', '五月', '六月', '七月',
            '八月', '九月', '十月', '十一月', '十二月'],
          axisLine: {
            lineStyle: {
              color: '#FFFFFF'
            }
          }
        },
        yAxis: {
          type: 'value',
          axisLine: {
            lineStyle: {
              color: '#FFFFFF'
            }
          }
        },
        tooltip: {
          tigger: 'item',
          formatter: "{b}: {c}条"
        },
        grid: {
          bottom: 35,
          left: 40
        },
        series: [{
          data: this.chartData,
          type: 'bar',
          areaStyle: {}
        }],
        color: ['#0BC3A9']
      };
    },
  },
  methods: {
    /**
     * 更新数据
     */
    updateData: function () {
      let option = {
        series: [
          {
            name: this.legend,
            data: this.chartData,
            type:"bar"
          },
        ],
      }
      this.chart.setOption(option);
    },
  },
  watch: {

    /**
     * 更新当前值
     * @param val
     */
    currentData: function (val) {
      this.chartData = val;
      this.updateData();
    },

    /**
     * 初始化数据
     * @param val
     */
    initData: function (val) {
      this.chartData = val;
      this.updateData();
    },

    /**
     * 是否进入loading
     * @param val
     */
    loading: function (val) {
      if (val) {
        this.chart.showLoading();
      } else {
        this.chart.hideLoading();
      }
    }
  },
  mounted() {
    this.chartData = this.currentData;
    this.updateData();
  }
}
</script>

<style scoped>
.chartSize {
  height: 100%;
  width: 100%;
}
</style>