<template>
  <div className="chartSize"></div>
</template>

<script>
import resize from "../../../components/echart/mixins/resize";

export default {
  mixins: [resize],
  props: {
    currentData: {
      type: Array,
      default: function () {
        return [{
          name: "a",
          value: 10
        }, {
          name: "b",
          value: 11
        }];
      }
    },
    title: {
      type: String,
      default: "图标标题",
    },
    initData: {
      type: Array,
      default: () => {
        return [];
      }
    },
    loading: {
      type: Boolean,
      default: false
    },
    chartName: {
      type: String,
      default: "年统计"
    },
    length: {
      type: Array,
      default: () => {
        return ["a", "b"]
      }
    }
  },
  data() {
    return {
      chartData: [1, 2, 3],
    };
  },
  computed: {
    option: function () {
      return {
        title: {
          text: '分类统计',
          textStyle: {
            color: "#FFFFFF"
          },
          left: 100,
        },
        tooltip: {
          tigger: 'item',
          formatter: "{a}<br/>{b}: {c}条({d}%)"
        },
        legend: {
          orient: 'vertical',
          data: this.legend,
          left: 0,
          top: 0,
          textStyle: {
            color: '#FFFFFF'
          }
        },
        series: [{
          name: "分类",
          type: 'pie',
          radius: '65%',
          data: this.currentData,
          label: {
            show: false,
            position: 'inside',
            formatter: "{a}:{c}",
            color: '#FFFFFF'
          },
          itemStyle: {
            opacity: 1
          },
          center: ['63%', '55%']
          // data: [5, 20, 36, 10, 10, 20,19,27]
        }],
        color: ['#027B82', '#0BC3A9', '#9FE6B8', '#FFDB5C', '#B03A5B']
      };
    },
  },
  methods: {
    /**
     * 更新数据
     */
    updateData: function () {
      let option = {
        series: [
          {
            name: this.legend,
            data: this.chartData,
            type: "pie"
          },
        ],
      }
      this.chart.setOption(option);
    },
    /**
     * 获取本月每一天
     * */
    getCountDays() {
      let curDate = new Date();
      let curMonth = curDate.getMonth();
      curDate.setMonth(curMonth + 1);
      curDate.setDate(0);
      return curDate.getDate();
    },
    /**
     * 获取本月每一天
     * */
    getEveryDay() {
      let dayArry = [];
      let day = this.getCountDays();
      for (let k = 1; k <= day; k++) {
        dayArry.push(k);
      }

      return dayArry;
    }
  },
  watch: {

    /**
     * 更新当前值
     * @param val
     */
    currentData: function (val) {
      this.chartData = val;
      this.updateData();
    },

    /**
     * 初始化数据
     * @param val
     */
    initData: function (val) {
      this.chartData = val;
      this.updateData();
    },

    /**
     * 是否进入loading
     * @param val
     */
    loading: function (val) {
      if (val) {
        this.chart.showLoading();
      } else {
        this.chart.hideLoading();
      }
    }
  },
  mounted() {
    this.chartData = this.currentData;
    this.updateData();
  }
}
</script>

<style scoped>
.chartSize {
  height: 100%;
  width: 100%;
}
</style>