<template>
  <div className="chartSize"></div>
</template>

<script>
import resize from "../../../components/echart/mixins/resize";

export default {
  mixins: [resize],
  props: {
    currentData: {
      type: Array,
      default: function () {
        return [1, 2, 3];
      }
    },
    title: {
      type: String,
      default: "图标标题"
    },
    initData: {
      type: Array,
      default: () => {
        return [];
      }
    },
    loading: {
      type: Boolean,
      default: false
    },
    chartName: {
      type: String,
      default: "年统计"
    }
  },
  data() {
    return {
      chartData: [1, 2, 3],
    };
  },
  computed: {
    option: function () {
      return {
        title:{
          text:'时间统计',
          textStyle:{
            color:'#FFFFFF'
          },
          left:100,
          top:15
        },
        tooltip:{
          tigger:'item',
          formatter:"{b}: {c}条"
        },
        xAxis: {
          type: 'category',
          boundaryGap: false,
          data: ["00:00","01:00","02:00","03:00","04:00","05:00","06:00","07:00",
            "08:00","09:00","10:00","11:00","12:00","13:00","14:00","15:00","16:00",
            "17:00","18:00","19:00",
            "20:00","21:00","22:00","23:00"],
          axisLine:{
            lineStyle:{
              color:'#FFFFFF'
            }
          }
        },
        yAxis: {
          type: 'value',
          axisLine:{
            lineStyle:{
              color:'#FFFFFF'
            }
          }
        },
        grid:{
          bottom:35,
          left:45
        },
        series: [{
          data: this.currentData,
          type: 'line',
          areaStyle: {}
        }],
        color:['#0BC3A9']
      };
    },
  },
  methods: {
    /**
     * 更新数据
     */
    updateData: function () {
      let option = {
        series: [
          {
            name: this.legend,
            data: this.chartData,
            type: "line"
          },
        ],
      }
      this.chart.setOption(option);
    },
    /**
     * 获取本月每一天
     * */
    getCountDays() {
      let curDate = new Date();
      let curMonth = curDate.getMonth();
      curDate.setMonth(curMonth + 1);
      curDate.setDate(0);
      return curDate.getDate();
    },
    /**
     * 获取本月每一天
     * */
    getEveryDay() {
      let dayArry = [];
      let day = this.getCountDays();
      for (let k = 1; k <= day; k++) {
        dayArry.push(k);
      }

      return dayArry;
    }
  },
  watch: {

    /**
     * 更新当前值
     * @param val
     */
    currentData: function (val) {
      this.chartData = val;
      this.updateData();
    },

    /**
     * 初始化数据
     * @param val
     */
    initData: function (val) {
      this.chartData = val;
      this.updateData();
    },

    /**
     * 是否进入loading
     * @param val
     */
    loading: function (val) {
      if (val) {
        this.chart.showLoading();
      } else {
        this.chart.hideLoading();
      }
    }
  },
  mounted() {
    this.chartData = this.currentData;
    this.updateData();
  }
}
</script>

<style scoped>
.chartSize {
  height: 100%;
  width: 100%;
}
</style>